$second-color: #f88971; //цвет спанов
$descr-size: 18px; //размер шрифта для абзацов


*{
  box-sizing: border-box;
}

body{
  width: 100%;
  padding-left: 90px;
  padding-right: 90px;
  font-family: Roboto, Arial, sans-serif;
  background: #FBF5F4;
  color: #000000;
  max-height: 969px;
}

ul,li{
  list-style: none;
  margin: 0;
  padding: 0;
}

a{
  text-decoration: none;
}

button{
  background: none;
  border: none;
  cursor: pointer;
}

.section{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.small-logo{
  height: 30px;
  width: 48px;
}

.big-logo{
  max-height: inherit;
}

.section_btn-circle{
  position: absolute;
  left: 0;
  top: 0;
  width: 60px;
  height: 60px;
  background: #f87b4d;
  border-radius: 50%;
  &:before, &:after{
    content: '';
    position: absolute;
    top: 28px;
    width: 20px;
    height: 2px;
    background: #ffffff;
  }
  &:before{
    left: 20px;
  }
  &:after{
    left: 20px;
    transform: rotate(-90deg);
  }
}

.counter{
  display: flex;
  flex-direction: column;
  text-align: right;
}

.counter>span{
  color: $second-color;
  &:first-child{
    font-size: 72px;
  }
  &:last-child{
    font-size: 30px;
    padding-right: 10px;
  }
}