@import "global.scss";

.section3_main-info{
  max-width: 440px;
}

.section3_title{
  max-width: 350px;
  margin-bottom: -30px; //!!!!!!!!!
  &>p>span{
    color: $second-color;
    font-size: 80px;
  }
  &>p{
    font-size: 48px;
  }
}

.section3_descr{
  font-size: $descr-size;
  line-height: 30px;
}

.section3_form{
  display: flex;
  flex-direction: column;
  &>input{
    border: none;
    background: none;
    height: 60px;
    width: 100%;
    border-bottom: 2px solid #c9c4c3;
    margin-bottom: 20px;
    font-size: 16px;
  }
}

.section3_agreement{
  font-size: 12px;
  max-width: 280px;
}

.section3_btn-circle{
  position: absolute;
  left: 0;
  top: 0;
  width: 60px;
  height: 60px;
  background: #f87b4d;
  border-radius: 50%;
}

.section3_btn-envelope{
  width: 20px;
  position: absolute;
  top: 23px;
  left: 20px;
}