@import "global.scss";

.section1_greeting{
  position: relative;
}

.section1_vertical-text{
  position: absolute;
  left: -75px;
  top: 100px;
  font-size: 24px;
  color: $second-color;
  transform: rotate(-90deg);
}

.section1_main-title{
  font-size: 200px;
  margin: 0;
  color: $second-color;
}

.section1_invite{
  display: flex;
  align-items: center;
}

.section1_invite-btn{
  position: relative;
  width: 60px;
  height: 60px;
  background: $second-color;
  border-radius: 50%;
  &:before, &:after{
    content: '';
    position: absolute;
    width: 20px;
    height: 2px;
    background: #ffffff;
  }
  &:before{
    left: 20px;
  }
  &:after{
    left: 20px;
    transform: rotate(-90deg);
  }
}

.section1_invite-title{
  font-size: $descr-size;
  margin-left: 20px;
  max-width: 330px;
}