.main-footer{
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding-top: 60px;
  padding-bottom: 60px;
  color: black;
}

.social{
  display: flex;
  justify-content: space-between;
  &>li>a{
    margin-right: 30px;
    color: black;
    font-size: 18px;
  }
}

.next-page{
  display: flex;
  justify-content: space-between;
}

.next-page_info{
  margin-right: 10px;
  font-size: 18px;
}

.next-page_btn{
  width: 40px;
  height: 40px;
  background: #ff6000;
  border-radius: 50%;
  margin-top: 5px;
  position: relative;
  &::before{
    position: absolute;
    content: "";
    left: 18px;
    top: 18px;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background: white;
  }
}