.main-header{
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding-top: 60px;
  padding-bottom: 60px;
  font-size: 18px;
  color: black;
}

.header_title{
  position: relative;
  &::before{
    position: absolute;
    content: "";
    left: -12px;
    top: 7px;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background: #ff6000;
  }
}